import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import {Router, Route} from 'react-router-dom'
import {createBrowserHistory} from 'history'

import MainComponent from './MainComponent.js'
import Auth from './Auth/Authorization'

import createStore from './store';

import socketClient from 'socket.io-client'

const socket = socketClient('http://localhost:2000', { query: "foo="+localStorage.Id })


const history = createBrowserHistory();


const store = createStore();

const errorFunction = (data, refs) => {
	for(var key in data){
		if (!data[key]) {
			if (key == 'Id' || key == 'DateId') {} else {
				refs[key].className = 'error';
				((key) => {setTimeout(() => {
					refs[key] ? refs[key].className = '' : console.log()
				}, 3000)})(key)
			}
		}
	}
}
const gg = (store) => {
	console.log(store)
	function requireAuth(nextState, replace, callback){

		var data = {
			...localStorage
		}

		var checkAuth = (res) => ({
			type: 'CHECK_AUTH',
			payload: res
		})
		var obj = JSON.stringify(data)
		console.log(obj)
		fetch('https://fierce-peak-99428.herokuapp.com/auth', {
			method: "POST", // POST, PUT, DELETE, etc.
			headers: new Headers({
				"Content-Type": "application/json" // for a string body, depends on body
			}),
			body: obj, // string, FormData, Blob, BufferSource, or URLSearchParams
			referrer: "about:client", // "" for no-referrer, or an url from the current origin
			mode: "cors", // same-origin, no-cors
			cache: "default",
		})
			.then((res) => {
					console.log(res)

				res.json().then(({res}) => {
					console.log(res)
					store.dispatch(checkAuth({res , acc: localStorage.account}))
				})
			})

		return <MainComponent errorFunction={errorFunction} socket={socket} />
	}
	return (
		<Router history = {history}>
			<Route path='/app/*' exact render={requireAuth} />
			<Route path='/' exact render={ () => <Auth socket={socket}/>}/>
		</Router>
	)
}

ReactDOM.render(
	<Provider store={store}>
		{gg(store)}
	</Provider>,
	document.getElementById('root')
);
