import React, {Component} from 'react';
import s from './Error.module.css'


class ErrorModal extends Component{

constructor(props) {
		super(props);
		this.state = {
			contant: '',
			flag: false
		};
	}
	
	componentDidMount() {
		if (this.props.showModal) {
			if (this.props.modal.openModalError){
				this.refs.overlay.style.display = 'block'
				this.refs.popup.style.display = 'block'
			}
		}
		this.setState({
			contant: this.props.contant
		})

		setTimeout(() => {
			this.refs.overlay ? this.handleClickClose() : console.log()
		}, 2000);
	}

	handleClickClose = (e) => {
		this.refs.overlay.style.display = 'none'
		this.refs.popup.style.display = 'none'
		this.setState({flag: true})
		this.props.setOpenModalError({flag: false});
	}

	render(){
		const {items} = this.props.modal;
		return(
			<div>
				<div ref="overlay" className={s.overlay} onClick={this.handleClickClose}></div>
				<div ref="popup" className={s.popup}>
					<div className={s.modalBody}>
						<span>{this.state.contant}</span>
					</div>
				</div>
			</div>
		)
	}
}

export default ErrorModal;
