import React, {Component} from 'react';
import s from './Main.module.css'

class Main extends Component {
	render(){
		return (
			<div>
				<div className={s.logo}>
					<img src="https://image.freepik.com/free-vector/house-logo-with-light-bulb-design_1438-129.jpg" alt="" />
					<div>
						<div>
							<div class="org">
								<span>Организация: ИП Перминова О.Г. г. Воронеж</span>
							</div>
							<div class="prog">
								<span>Web-приложение "Склад"</span>
							</div>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default Main;
